body {
  text-transform: uppercase;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

.nav {
  display: flex;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  margin-left: 20px;
  text-decoration: none;
  display: flex;
}

.nav__link:first-child {
  margin-left: 0;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__link.is-active {
  color: #00acdc;
  text-align: center;
  position: relative;
}

.nav__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
